import React, { Component } from 'react'
import { Card } from 'antd';
const { Meta } = Card;

export default class WorkCard extends Component {
    render() {
        return (
            <div>
                <Card
                    hoverable
                    style={{ width: 300 }}
                    cover={<img alt="example" src="images/12.png" />}>
                    <Meta title="Europe Street beat" description="www.instagram.com" >
                        
                    </Meta>
                    <div>
                        <a href="https://www.baidu.com/" target="_blank" rel="noopener noreferrer">
                            Navigation Work - Link
                        </a>
                    </div>
                </Card>
            </div>
        )
    }
}
