import React, { Component } from 'react'
import { Row, Col } from 'antd';
import './Create.less'
import WorkCard from '../../components/workCard/WorkCard';

export default class Create extends Component {
    render() {
        return (
            <div>
                <Row style={{marginTop:'50px'}}>
                    <Col span={12}><WorkCard /></Col>
                    <Col span={12}><WorkCard /></Col>
                </Row>
            </div>
        )
    }
}
