import logo from './logo.svg';
import './App.css';
import React, { Component } from 'react'
import { Button, Menu } from 'antd';
import Transform from './pages/work1/Transform';
import Introduction from './pages/author/Introduction';
import Create from './pages/work23/Create';
import { Tabs } from 'antd';

const { TabPane } = Tabs;
let navigate=["Authors","Work1","Work2&3"]
export default class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      content: <Introduction />
    }
  }

  changeContent = (value) => {
    let tempContent;
    switch (value) {
      case "0":
        tempContent=<Introduction />
        break
      case "1":
        tempContent=<Transform/>
        break;
      case "2":
        tempContent=<Create/>
        break
      default:
        break
    }
    this.setState({
      content: tempContent
    }
    )
  }

  render() {
    const { content } = this.state
    return (
      <div className="App">
        <div className='div-header'></div>
        <Tabs defaultActiveKey="0" tabPosition={"left"} onChange={this.changeContent}>
          {[...Array.from({ length: 3 }, (v, i) => i)].map(i => (
            <TabPane tab={`${navigate[i]}`} key={i}>
                {content}
            </TabPane>
          ))}
        </Tabs>
      </div>
    )
  }
}